import React, { Component } from "react";
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import markerIcon from "./marker.svg";

class Map extends Component {
  componentDidMount() {
    // Удаляем элемент с идентификатором mapid, если он уже существует
    if (document.getElementById("mapid")) {
      document.getElementById("mapid").remove();
    }

    // Создаем новый элемент mapid и добавляем его на страницу
    const mapElement = document.createElement("div");
    mapElement.id = "mapid";
    mapElement.style.height = "600px";
    this.mapContainer.appendChild(mapElement);

    // Создаем карту и добавляем ее на страницу
    this.map = L.map(mapElement).setView([51.505, -0.09], 13);

    // Добавляем слой тайлов OpenStreetMap
    L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
      attribution:
        'Map data © <a href="https://openstreetmap.org">OpenStreetMap</a> contributors',
      maxZoom: 18,
    }).addTo(this.map);

    const customIcon = L.icon({
      iconUrl: markerIcon,
      iconSize: [25, 41],
      iconAnchor: [12, 41],
      popupAnchor: [0, -41],
      shadowSize: [41, 41],
    });

    // Добавляем маркер на карту
    L.marker([55.74639, 38.417711], { icon: customIcon })
      .addTo(this.map)
      .bindPopup("Склад Элекросталь")
      .openPopup();
    L.marker([55.848721, 48.842408], { icon: customIcon })
      .addTo(this.map)
      .bindPopup("Склад Казань")
      .openPopup();
    L.marker([45.054455, 39.068873], { icon: customIcon })
      .addTo(this.map)
      .bindPopup("Склад Краснодар")
      .openPopup();
    L.marker([54.941901, 82.916459], { icon: customIcon })
      .addTo(this.map)
      .bindPopup("Склад Новосибирск")
      .openPopup();
    L.marker([55.38617, 37.583832], { icon: customIcon })
      .addTo(this.map)
      .bindPopup("Склад Коледино")
      .openPopup();
  }

  render() {
    return <div ref={(el) => (this.mapContainer = el)}></div>;
  }
}

export default Map;
