import "./style.scss";

import Cards from "./../Cards/Cards";
import CardsNone from "./../CardsNone/CardsNone";
import Map from "../Map/Map";
import { useState } from "react";

const Home = ({
  createCard,
  cardsData,
  setCardsData,
  cardСompute,
  cardEdit,
  backToCards,
  setActiveCard,
  setCardsLastIndex,
}) => {
  const [homePage, setHomePage] = useState("cards");

  const cards = () => {
    if (cardsData.length === 0 || Object.keys(cardsData[0]).length === 0) {
      return <CardsNone createCard={createCard} />;
    } else {
      return cardsData.map((card, id) => {
        return (
          <Cards
            createCard={createCard}
            cardsData={cardsData}
            setCardsData={setCardsData}
            cardСompute={cardСompute}
            cardEdit={cardEdit}
            card={card}
            setActiveCard={setActiveCard}
            setCardsLastIndex={setCardsLastIndex}
            id={id}
            key={id}
          />
        );
      });
    }
  };

  const showMap = () => {
    setHomePage("map");
  };
  const showCards = () => {
    setHomePage("cards");
  };

  const showData = () => {
    console.log(cardsData);
  };

  return (
    <div className="container">
      <nav className="nav">
        <ul>
          <li>
            <button onClick={backToCards}>
              <img src="./img/MIK.svg" alt="mik" />
            </button>
          </li>
          <li>
            <a href="!#" onClick={showCards}>
              Карточки
            </a>
          </li>
          <li>
            <a href="!#" onClick={showMap}>
              Карта складов
            </a>
          </li>
          <li>
            <a href="!#" onClick={showData}>
              Инструкции
            </a>
          </li>
          <li>
            <a href="!#">Войти</a>
          </li>
        </ul>
      </nav>

      <div className="cards">{homePage === "map" ? <Map /> : cards()}</div>
    </div>
  );
};

export default Home;
